.header-container {
    display: flex;
    height: 60px;
    margin: 0;
    min-width: 0;
    text-align: center;
    border-style: none none solid none;
    /*border-color: rgb(226, 226, 226);*/
    border-color: #d0c9f7;
    overflow: hidden;
}

.logo-container {
    flex: 0 1 5%;
    width: 100px;
    cursor: pointer;
}

.right-container {
    display: flex;
    flex: 0 0 95%;
    /*background-color: #F6F6F8;*/
    background-color: #eae7fc;
    justify-content: space-between;
    align-items: center;
    font-family: 'IBM Plex Serif', serif;
    padding: 20px;
    font-size: 17px;
    /*color: gray;*/
    color: #8e7fdf;
}

.shows-books-films {
    margin-right: 15px;
}



.logo-container {
    width: 60px;
    height: 60px;
    background-image: linear-gradient(lightgray, black);
}

.logo {
    width: 100%;
    height: 100%;
    image-rendering: pixelated;
    image-rendering: -moz-crisp-edges;
    image-rendering: crisp-edges;
}

.logo:hover {
    content: url(data:image/gif;base64,);
}

.logo:active {
    content: url(data:image/gif;base64,);
}
.label {
    font-size: 0px;
    display: flex;
    flex-direction: column;
}

.inner-label {
    align-items: center;
    display: flex;
    margin: 20px 20px 0px 95px;
    height: 35px;
    font-family: 'IBM Plex Serif', serif;
    font-size: 15px;
}

.grid-inner-label {
    align-items: center;
    display: flex;
    margin: 20px 20px 0px 75px;
    height: 35px;
    font-family: 'IBM Plex Serif', serif;
    font-size: 15px;
}

.device-number {
    flex: 0 1 10.5%;
    padding-left: 3vw;
    color: rgb(199, 199, 199);
}

.media-type-label {
    flex: 0 1 24.3%;
    font-weight: bold;
}

.name-label {
    flex: 0 1 30%;
    font-weight: bold;
}

.list-label-hr {
    width: 92.8%;
    margin-left: 80px;
}
* {
    margin: 0;
    padding: 0;
}

.toolbar-container {
    font-size: 0;
}

.before-hr {
    display: flex;
    align-items: center;
}

.search-container {
    margin: 12px 0px 12px 30px;
    flex: 0 0 90%;
}

.list-view-container {
    flex: 0 0 2.5%;
    cursor: pointer;
}

.grid-view-container {
    flex: 0 0 2.5%;
    cursor: pointer;
}

.filter-text-container {
    flex: 0 0 2.5%;
    cursor: pointer;
}

.filter-text-container p {
    font-size: 15px;
    font-family: 'IBM Plex Serif', serif;
    color: gray;
}

.search-bar {
    font-family: 'IBM Plex Serif', serif;
    outline: none;
    border: 0px;
    color:gray;
    border-radius: 5px;
    height: 30px;
    padding: 5px 5px 5px 40px;
    width: 30%;
    font-size: 16px;
    background: rgb(245, 245, 245) url(data:image/png;base64,) no-repeat center left 5px;
    background-size: 1.5vw;
}

/* Note: the clear button will appear once the user types rather than in an empty input field*/
.search-bar::-webkit-search-cancel-button{
    -webkit-appearance: none;
    appearance: none;
    height: 10px;
    width: 10px;
    background-image: url(data:image/png;base64,);
    background-size: 10px 10px;
}

input::-webkit-input-placeholder{
    color: gray;
}

input::placeholder{
    color: gray;
}

img {
    width: 30px;
    height: auto;
}

.filter-overlay {
    background: white;
    width: 300px;
    height: auto;
    padding-bottom: 2vw;
    box-shadow: 0 3px 20px rgb(0 0 0 / 0.2);
    position: absolute;
    top: 61.5px;
    right: 0px;
    font-family: 'IBM Plex Serif', serif;
}

.filter-x {
    display: flex;
    justify-content: space-between;
    border-style: none none solid none;
    border-color: rgb(226, 226, 226);
    height: 54px;
    padding-left: 23px;
    padding-right: 23px;
    align-items: center;
    font-size: 15px;
    color:#4f4f4f;
    cursor: pointer;
}

.filter-x img {
    width: 25px;
    height: 25px;
}

.media-type {
    margin-left: 28px;
    margin-top: 25px;
    margin-bottom: 30px;
    font-size: 16px;
    font-weight: bold;
}

.media-type-options {
    margin-left: 30px;
    margin-top: 10px;
}
.list-icon-default {
    width: 25px;
    height: auto;
    border-radius: 0.2vw;
}
.grid-icon-default {
    width: 25px;
    height: auto;
    border-radius: 0.2vw;
}
.checkbox-container {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
    color: #5c5c5c;
}

.checkbox-container img {
    width: 20px;
    height: 20px;
}

.checkbox-container p {
    font-size: 14px;
}
.card {
    margin-left: auto;
    margin-right: auto;
    margin: 0px 40px 0px 80px;
    font-size: 0px;
    cursor: pointer;
}

.card:hover{
    background: rgb(249, 249, 249);
}

.inner-card {
    margin-left: 60px;
    height: 55px;
    display: flex;
    align-items: center;
    font-family: 'IBM Plex Serif', serif;
    font-size: 13px;
    color: gray;
}

.image {
    flex: 0 1 6%;
    box-sizing: border-box;
    height: 100%;
    padding: 0.3vw;
}

.image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 0.2vw;
}

.media-tablelist-type {
    flex: 0 1 25%;
    padding-left: 5vw;
}

.name {
    flex: 0 1 60%;
}

hr {
    height: 2px;
    background-color: rgb(237, 237, 237);
    border: none;
}
.grid-card {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    background-color: #F6F6F8;
    margin: 15px;
    border-radius: 10px;
    width: 280px;
    height: 225px;
    border: 1.5px solid rgb(225, 225, 225);
    cursor: pointer;
}

.grid-image {
    display: flex;
    justify-content: center;
    height: 145px;
    align-items: center;
}

.grid-image img {
    width: 100px;
    height: 85%;
    object-fit: contain;
}

.grid-label {
    background-color: white;
    height: 80px;
    border-radius: 0px 0px 10px 10px;
    font-family: 'IBM Plex Serif', serif;
    padding-right: 11px;
}

.grid-name {
    margin-top: 13px;
    margin-left: 15px;
    color: rgb(101, 101, 101);
}

.grid-media-type {
    margin-top: 5px;
    margin-left: 15px;
    font-size: 13px;
    color: rgb(142, 142, 142);
}
.table-list-container {
    height: 665px;
    overflow-y: scroll;
}

.grid-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}
.single-item-toolbar-container {
    display: flex;
    font-family: 'IBM Plex Serif', serif;
    align-items: center;
    border-style: none none solid none;
    border-color: rgb(226, 226, 226);
    height: 56px;
}

.single-item-toolbar-container img {
    width: 20px;
    height: 20px;
    padding-left: 30px;
    cursor: pointer;
}

.single-item-toolbar-container p {
    margin-left: auto;
    margin-right: auto;
    font-size: 15px;
    color: rgb(140, 140, 140);
}
.single-item-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 795px;
    overflow: hidden;
    grid-gap: 70px;
    gap: 70px;
}

.single-item-image {
    width: 250px;
    height: auto;
}
.single-item-description-container {
    display: flex;
    justify-content: space-between;
    width: 350px;
    height: auto;
    padding: 0.2vw;
    font-family: 'IBM Plex Serif', serif;
    font-size: 15px;
    color: rgb(111, 111, 111);
    border-style: none none solid none;
    border-color: rgb(237, 237, 237);
    margin: 10px;
    text-align: right;
}
